export enum RoundingMethod {
  DEFAULT = "DEFAULT",
  UP = "UP",
  DOWN = "DOWN",
}

export function formatNumber(
  num: number,
  options: { showDecimals: boolean; roundingMethod?: RoundingMethod } = {
    showDecimals: false,
    roundingMethod: RoundingMethod.DEFAULT,
  },
): string {
  num = Number(num);
  if (num == Infinity) {
    return "";
  }
  if (num >= 1e24) {
    return (
      formatNumber(num / 1e24, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "Sp"
    );
  }
  if (num >= 1e21) {
    return (
      formatNumber(num / 1e21, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "Sx"
    );
  }
  if (num >= 1e18) {
    return (
      formatNumber(num / 1e18, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "Qi"
    );
  }
  if (num >= 1e15) {
    return (
      formatNumber(num / 1e15, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "Qa"
    );
  }
  if (num >= 1000000000000) {
    return (
      formatNumber(num / 1000000000000, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "T"
    );
  }
  if (num >= 1000000000) {
    return (
      formatNumber(num / 1000000000, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "B"
    );
  }
  if (num >= 1000000) {
    return (
      formatNumber(num / 1000000, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "M"
    );
  }
  if (num >= 10000) {
    return (
      formatNumber(num / 1000, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "K"
    );
  }

  if (options.showDecimals && Math.abs(num) < 10 && !Number.isInteger(num)) {
    const base =
      options.roundingMethod != RoundingMethod.UP
        ? Math.floor(num * 100)
        : Math.ceil(num * 100);
    return (base / 100.0).toFixed(2);
  } else if (
    options.showDecimals &&
    Math.abs(num) < 100 &&
    !Number.isInteger(num)
  ) {
    const base =
      options.roundingMethod != RoundingMethod.UP
        ? Math.floor(num * 10)
        : Math.ceil(num * 10);
    return (Math.floor(base) / 10.0).toFixed(1);
  } else {
    const base =
      options.roundingMethod != RoundingMethod.UP
        ? Math.floor(num)
        : Math.ceil(num);
    return base.toFixed();
  }
}

export function formatTime(
  secs: number,
  options: { shortForm: boolean; showDecimals: boolean } = {
    shortForm: false,
    showDecimals: false,
  },
): string {
  const { shortForm, showDecimals } = options;
  let unit = shortForm ? "sec" : "second(s)";
  let time = formatNumber(secs, { showDecimals: showDecimals });
  if (secs > 60) {
    unit = shortForm ? "min" : "minute(s)";
    time = formatNumber(secs / 60, { showDecimals: showDecimals });
  }
  if (secs > 60 * 60) {
    unit = shortForm ? "hr" : "hour(s)";
    time = formatNumber(secs / (60 * 60), { showDecimals: showDecimals });
  }
  return `${time} ${unit}`;
}

export function formatTimeLong(duration: moment.Duration) {
  if (duration.asDays() < 1.0) {
    return `${duration.hours().toFixed().padStart(2, "0")}:${duration
      .minutes()
      .toFixed()
      .padStart(2, "0")}:${duration.seconds().toFixed().padStart(2, "0")}`;
  }

  return `${Math.floor(duration.asDays()).toFixed()} days, ${duration
    .hours()
    .toFixed()
    .padStart(2, "0")}:${duration
    .minutes()
    .toFixed()
    .padStart(2, "0")}:${duration.seconds().toFixed().padStart(2, "0")}`;
}
